import React, { Component } from 'react';
import { fadeIn } from 'react-animations'
import Radium, {StyleRoot} from 'radium';

const styles = {
	fadeIn: {
		animation: 'x 500ms',
		animationName: Radium.keyframes(fadeIn, 'fadeIn')
	}
}

class EmptyPane extends Component {

	constructor(props){
		super(props)
	}
	render() {
		let mainMessage = "Pas d'lments";
		if(this.props.mainMessage){
			mainMessage = this.props.mainMessage;
		}

		let className = "admin-list-nocontent";
		if(this.props.className){
			className = this.props.className
		}

		let secondaryMessage = "Pas d'lments";
		if(this.props.secondaryMessage){
			secondaryMessage = this.props.secondaryMessage;
		}

		let rootClassName = this.props.className ? this.props.className + ' empty-pane' : 'empty-pane'
		return  <div className={rootClassName}>
			<StyleRoot style={styles.fadeIn}>
				<p className='empty-pane-secondary-message'>{secondaryMessage}</p>
			</StyleRoot>
		</div>
	}
}


export default EmptyPane;
